body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav {
	padding: 0 1rem;
}

.content {
	min-height: calc(100vh - 70px - 64px);
	padding: 1.5rem 0;
}

.goods {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
	gap: 1rem;
}

.card {
	display: flex;
	flex-direction: column;
}

.card-content {
	flex-grow: 1;
}

.cart {
	position: fixed;
	bottom: 2rem;
	right: 2rem;

	cursor: pointer;
	z-index: 5;
	padding: 1rem;

	border-radius: 1rem;
}
@media (min-width: 767px) {
	.cart {
		top: 5rem;
		bottom: unset;
	}
}

.cart-list {
	position: fixed;
	top: 50%;
	left: 50%;

	/*Смещение в % в функции translate происходит относительно размеров самого элемента*/
	transform: translate(-50%, -50%);

	background-color: white;
	width: 50rem;
	max-width: 95%;

	box-shadow: inset -1px 3px 8px 5px #1f87ff, 2px 5px 16px 0px #0b325e, 5px 5px 15px 5px rgba(0, 0, 0, 0);

	animation: show 500ms ease-in-out;
}

.collection-item {
	position: relative;
}

.pointer {
	cursor: pointer;
}

@keyframes show {
	from {
		top: 70%;
		opacity: 0;
	} to {
		top: 50%;
		opacity: 1;
	}
}

.cart-item-icon {
	vertical-align: middle;
	user-select: none;
}

.cart-item-icon :not(.secondary-content) {
	padding: 0.5rem;
}

.cart-button {
	position: absolute;
  top: 50%;
	right: 10px;
  transform: translateY(-50%);
}

#toast-container {
	top: 3rem !important;
	animation: fade-in 250ms ease-in-out;
}

@keyframes fade-in {
	from {
		opacity: 0;
		transform: translateY((2rem));
	}
	to {
		opacity: 1;
		transform: translateY(0rem);
	}
}
